.my__swiper{
    width: 100%;
    height: 100%;
    margin: 1rem auto;
    border-radius: 15px;
}

.slider img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.swiper-button-next,
.swiper-button-prev {
    transform: scale(0.5);
    color: rgb(184, 122, 243);
    font-weight: 600;
}